import React, {Component} from 'react';
import '../App.css'
import Portfolio from '../components/Portfolio'
import Header from '../components/Header'
import About from '../components/About'
import Skill from './Skill';

class Navbar extends Component {
    constructor(props) {
        super(props);
        this.toggleNavbar = this
            .toggleNavbar
            .bind(this);
        this.state = {
            collapsed: true
        };
    }
    toggleNavbar() {
        this.setState({
            collapsed: !this.state.collapsed
        });
    }
    render() {
        const collapsed = this.state.collapsed;
        const classOne = collapsed
            ? 'collapse navbar-collapse'
            : 'collapse navbar-collapse show';
        const classTwo = collapsed
            ? 'navbar-toggler navbar-toggler-right collapsed'
            : 'navbar-toggler navbar-toggler-right';

        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
                    <a className="navbar-brand js-scroll-trigger log" href="#home" title="Home">    
                    MR      
                    </a>
                    <button
                        onClick={this.toggleNavbar}
                        className={`${classTwo}`}
                        type="button"
                        data-toggle="collapse"
                        data-target="#navbarResponsive"
                        aria-controls="navbarResponsive"
                        aria-expanded="false"
                        aria-label="Toggle navigation">
                        <i className="fa fa-bars"></i>
                    </button>
                    <div className={`${classOne}`} id="navbarResponsive">
                        <ul className="navbar-nav text-uppercase ml-auto">
                            <li className="nav-item">
                                <a className="nav-link js-scroll-trigger colrs" href="#about">About</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link js-scroll-trigger colrs" href="#skill">Skills</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link js-scroll-trigger colrs" href="#portfolio">Portfolio</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link js-scroll-trigger colrs" href="#contact">Contact</a>
                            </li>
                        </ul>
                    </div>
                    {/* </div> */}
                </nav>

                <Header/>
                <About/>
                 <Skill/>
                <Portfolio/>
            </div>
        );
    }
}

export default Navbar